import React from 'react'
import { Image } from 'semantic-ui-react'
import { StyledAdminStage } from './style'
import logo from '../../../images/ripple/ripple-logo-full.png'

const DashLogo = () => <StyledAdminStage><Image src={logo} fluid /></StyledAdminStage>


export default DashLogo


